import React from "react"
import { graphql } from "gatsby"
import Layout from '../../components/layout'
import { navLinkText, segment, heading } from "../../components/layout.module.css"
import { MDXRenderer } from 'gatsby-plugin-mdx'
import { Button, Grid } from '@mui/material'
import resume from "../../static/Resume20220215.pdf"

const portfolioPage = ({ data }) => {
  return(
      <Layout pageTitle='Portfolio'>
        <h1 className={heading}>Portfolio</h1>
        <Grid container justifyContent="flex-end">
          <Button variant="contained" href="/Resume20220215.pdf" target="_blank">
            Resume
          </Button>
        </Grid>
        <hr className={segment}></hr> {
          data.allMdx.nodes.map((node) => (
            <article key={node.id}>
              <h2 className={navLinkText}>
                  {node.frontmatter.title}
              </h2>
              <p>Date Posted: {node.frontmatter.date}</p>
              <MDXRenderer>
                  {node.body}
              </MDXRenderer>
            </article>
          ))
        }
      </Layout>
  )
}

export const pageQuery = graphql`
  query {
    allMdx(sort: {fields: id, order: DESC}) {
      nodes {
        frontmatter {
          date(formatString: "YYYY-MM-DD")
          title
        }
        id
        slug
        body
      }
    }
  }
`
export default portfolioPage
